.9,-30.8,-63.7,-46.1,-49,-59.3C-34.3,-72.6,-17.2,-83.8,0.5,-84.4C18.2,-85.1,36.5,-75.1,49.3,-61.6Z")}75%{d:path("M49,-57.2C60.1,-49,63.6,-30.4,60,-15.7C56.5,-1.1,46,9.7,37.7,19.7C29.5,29.7,23.5,38.9,15.1,42.1C6.8,45.4,-3.9,42.7,-17.5,40.9C-31.1,39,-47.6,38,-61.3,28.5C-75,19,-85.9,1,-85.3,-17.2C-84.7,-35.4,-72.6,-53.7,-56.4,-61C-40.3,-68.4,-20.2,-64.7,-0.6,-63.9C18.9,-63.1,37.8,-65.3,49,-57.2Z")}100%{d:path("M45.1,-50.8C54.3,-46,54.6,-27.9,55.3,-11.6C55.9,4.7,56.8,19,51.2,30.4C45.6,41.8,33.5,50.3,18.5,59.3C3.6,68.4,-14.2,78.1,-32,76.5C-49.7,75,-67.5,62.1,-71.3,45.8C-75.1,29.6,-64.9,10,-56.2,-4.9C-47.6,-19.8,-40.6,-29.9,-31.5,-34.7C-22.4,-39.4,-11.2,-38.9,3.4,-42.9C18,-47,35.9,-55.6,45.1,-50.8Z")}}

.navbar{padding:1vmin;background-color:var(--primaryColor);display:flex;justify-content:flex-end}.navbar .nav-item{padding:2vmin}.navbar .logo-text{font-size:3vmin;font-weight:500;color:var(--textColor)}

.selection-box{min-width:300px;flex:0 1 40%;outline:2px solid white;border-radius:40px;cursor:pointer;transition:1s;display:flex;flex-direction:column;align-items:center;justify-content:center}.selection-box h1{color:var(--textColor);font-size:5vmin}.selection-box:hover{transform:translateY(-10%)}

.test-node-enter{opacity:0;transform:translateY(50%)}.test-node-enter-active{opacity:1;transform:translateY(0%);transition:opacity 1s, transform 1s}.test-node-exit{opacity:1;transform:translateY(0%)}.test-node-exit-active{opacity:0;transform:translateY(50%);transition:opacity 1s, transform 1s}.side-node-enter{opacity:0;transform:translateX(50%)}.side-node-enter-active{opacity:1;transform:translateX(0%);transition:transform 1s, opacity 1s}.side-node-exit{opacity:1;transform:translateX(0%)}.side-node-exit-active{opacity:0;transform:translateX(-50%);transition:transform 1s, opacity 1s}

.form-container{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;padding-bottom:10%}.form-container form{width:50%;height:90%;display:flex}.form-container form>label,.form-container form input{margin:1%}.form-container form .form-input__text{width:50%;font-family:Exil;font-size:5vmin;text-align:center;border-radius:30px;border:none;outline:none}.form-container form .form-label{font-size:7vmin}.form-container form .form-submit{font-size:3vmin;font-family:Exil;background-color:transparent;color:var(--textColor);padding:1%;width:20%;border:2px solid white;outline:none;transition:1s}.form-container form .form-submit:hover,.form-container form .form-submit:focus{width:30%;background-color:var(--textColor);color:var(--primaryColor)}.form-container .center-form{display:flex;flex-direction:column;align-items:center;justify-content:center}.correct{box-shadow:5px 10px 18px green}.incorrect{animation:incorrectBounce 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) 1}@keyframes incorrectBounce{10%,90%{transform:translate3d(-1px, 0, 0)}20%,80%{transform:translate3d(2px, 0, 0)}30%,50%,70%{transform:translate3d(-4px, 0, 0);box-shadow:5px 10px 18px red}40%,60%{transform:translate3d(4px, 0, 0)}}.info-screen{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.info-screen h1{font-size:7vmin}.info-screen .class-code{font-size:20vmin;margin:0px;line-height:20vmin}

:root{--bg: #fafdf6;--primaryColor: #000000;--secondaryColor: #b279a7;--tertiaryColor: #D387AB;--color4: #e899dc;--textColor: white}html,body,#root{height:100%;margin:0;color:white;color:var(--textColor);overflow:none}.App{height:100%}.panel-bg{display:flex;flex-flow:column;width:100%;height:100%}.test{background-color:pink;flex:1 1 auto}.unselectable{-webkit-user-select:none;-ms-user-select:none;user-select:none}a{color:inherit;text-decoration:none}

